.mainWrapper {
    @apply flex flex-row justify-between items-center px-3 py-2;
}

.logoImage {
    @apply h-10 w-full;
}

.linkIcon {
    @apply text-xl ml-1.5;
}

.mainLinksWrapper {
    @apply flex flex-row justify-between items-center ml-auto mr-5;
}

.mainLinks {
    @apply relative text-[15px] font-bold ml-3.5 flex flex-row items-center justify-between p-2 py-3;
}

.mainMenu1 {
    @apply absolute z-10 flex flex-col top-2 w-44 invisible h-auto opacity-0 shadow-none drop-shadow-none p-2 rounded duration-500 ease-in-out;
}

.subMenu1 {
    @apply absolute ml-12 left-3/4 -top-2 z-20 flex flex-col top-0 w-52 invisible h-auto opacity-0 shadow-none drop-shadow-none p-2 rounded duration-500 ease-in-out;
}

.subMenu2 {
    @apply absolute ml-12 left-3/4 -top-2 z-20 flex flex-col top-0 w-52 invisible h-auto opacity-0 shadow-none drop-shadow-none p-2 rounded duration-500 ease-in-out;
}

.mainHover1:hover .mainMenu1 {
    @apply visible h-auto w-44 top-9 opacity-100 shadow-md;
}

.topBorder {
    @apply w-0 h-0.5 duration-500 ease-in-out -mx-2 -mt-0.5;
}

.mainHover1:hover .topBorder {
    @apply w-44 h-0.5;
}

.subHover1:hover .subMenu1 {
    @apply visible h-auto w-52 ml-12 left-3/4 -top-2 opacity-100 shadow-md;
}

.topBorder1 {
    @apply w-0 h-0.5 duration-500 ease-in-out -mx-2 -mt-0.5;
}

.subHover1:hover .topBorder1 {
    @apply w-52 h-0.5;
}

.subHover2:hover .subMenu2 {
    @apply visible h-auto w-52 ml-12 left-3/4 -top-2 opacity-100 shadow-md;
}

.topBorder2 {
    @apply w-0 h-0.5 duration-500 ease-in-out -mx-2 -mt-0.5;
}

.subHover2:hover .topBorder2 {
    @apply w-52 h-0.5;
}

.subLinks {
    @apply relative mx-2 py-2.5 border-b-[0.5px] text-[15px];
}

.accountWrapper {
    @apply flex flex-row justify-between items-center mr-6;
}

.accountIcon {
    @apply mx-1 text-xl;
}

.accountLink {
    @apply mx-1 text-opacity-80 font-bold duration-300 ease-in-out;
}

.accountOr {
    @apply text-sm text-opacity-80 font-semibold;
}

.buttonWrapper {
    @apply flex flex-row justify-between items-center px-4 py-3 rounded-3xl shadow-md drop-shadow-lg;
}

.buttonText {
    @apply font-bold ml-1.5;
}