.footerWrapper {
    
}

.footerSubWrapper {
    @apply grid gap-10 p-20;
    @apply md:grid-cols-2;
    @apply lg:grid-cols-4;
}

.footerSection {

}

.footerLinksTitle {
    @apply relative text-xl font-bold flex flex-row items-center justify-start; 
}

.subMenu {
    @apply absolute top-4 z-20 flex flex-col top-0 w-52 invisible h-auto opacity-0 shadow-none drop-shadow-none p-2 rounded duration-500 ease-in-out;
}

.subHover:hover .subMenu {
    @apply visible h-auto w-52 top-8 opacity-100 shadow-md;
}

.subLinks {
    @apply relative mx-2 text-sm py-1 flex flex-row justify-between items-center duration-300 ease-in-out;
}

.hoverLinks {
    @apply flex flex-row justify-between items-center w-full;
}

.subMenu1 {
    @apply absolute ml-12 left-3/4 -top-2 z-20 flex flex-col top-0 w-52 invisible h-auto opacity-0 shadow-none drop-shadow-none p-2 rounded duration-500 ease-in-out;
}

.subHover1:hover .subMenu1 {
    @apply visible h-auto w-52 ml-12 left-3/4 -top-2 opacity-100 shadow-md;
}

.footerLinksWrapper {
    @apply flex flex-col justify-center items-start mt-8;
}

.footerLinks {
    @apply duration-300 ease-in-out font-semibold text-sm mb-2.5;
}

.footerTitle {
    @apply font-bold text-[40px] mb-8;
}

.footerText {
    @apply font-bold text-sm mb-2;
}

.socialWrapper {
    @apply flex flex-row justify-start items-center mt-12;
}

.helpWrapper {
    @apply rounded-2xl flex flex-col justify-center items-center w-full px-9 py-7;
}

.helpIconWrapper {
    @apply w-full mb-11;
}

.helpIcon {
    @apply text-7xl mx-auto;
}

.helpTitle {
    @apply text-lg font-bold text-center mt-1;
}

.helpContact {
    @apply mt-3 font-normal;
}

.helpEmailWrapper {
    @apply flex flex-row justify-center items-center mt-1.5;
}

.helpEmail {
    @apply text-base font-semibold ml-2.5 mb-0.5;
}

.footerBottom {
    @apply py-10 px-20 grid gap-10;
    @apply md:grid-cols-2;
    @apply lg:grid-cols-3;
    
}

.footerBottomLinks {
    @apply duration-300 ease-in-out text-sm font-semibold mr-10;
}

.footerCopyright {
    @apply text-sm font-bold;
}